.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 500px;
    padding: 40px;

    background-color: $color-deep-sky-blue;
    color: $color-default-white;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  h2 {
    margin: 0;
    padding: 0;
    margin-bottom: 15px;
    font-family: $ff-roboto-condensed;
    font-size: 30px;
    line-height: 140%;
    font-weight: 400;
  }

  p {
    margin: 0;
    padding: 0;
    margin-bottom: 40px;
    line-height: 22px;
  }

  &__close-btn {
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.modal__form .form__footer {
  flex-direction: column;
  align-items: center;
}

.modal__form--footer .form__button {
  width: 100%;
  margin-bottom: 20px;
}

.modal__form .modal__form--footer label {
  width: 100%;
}
