.featuries {
  padding: 0;
  padding-top: 120px;
  max-width: 1222px;

  @include vp-1023 {
    padding-top: 83px;
    padding-bottom: 76px;
  }

  @include vp-767 {
    padding-bottom: 46px;
    padding-top: 50px;
  }
}

.featuries__list {
  margin: 0;
  padding: 0;
  /* margin-left: 35px;
  margin-bottom: 75px; */
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 300px);
  column-gap: 31px;
  row-gap: 30px;

  @include vp-1023 {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 360px);
  }

  @include vp-767 {
    width: 100%;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 340px) repeat(2, 325px);
  }
}

.featuries__item {
  padding-top: 40px;
  padding-left: 40px;
  padding-right: 25px;
  padding-bottom: 68px;
  border: 1px solid $color-whisper;
  border-left: 5px solid $color-deep-sky-blue;

  @include vp-1023 {
    padding-right: 25px;
  }

  @include vp-767 {
    min-width: 100%;
    padding-top: 30px;
    padding-bottom: 55px;
    padding-left: 25px;
    padding-right: 25px;

    & svg {
      width: 30px;
      height: 30px;
    }
  }

  & svg {
    fill: $color-deep-sky-blue;
  }

  & h3 {
    margin: 0;
    margin-top: 14px;
    margin-bottom: 15px;
    font-family: $ff-roboto-condensed;
    font-size: 24px;
    line-height: 140%;

    color: $color-deep-sky-blue;

    @include vp-1023 {
      margin-top: 9px;
      margin-bottom: 11px;
      line-height: 135%;
    }

    @include vp-767 {
      font-size: 20px;
      line-height: 140%;
    }
  }

  & p {
    margin: 0;
    line-height: 135%;
    color: $color-black-pearl;

    @include vp-1023 {
      font-size: 16px;
      line-height: 135%;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 135%;
    }
  }

  @include hover {
    border-color: $color-deep-sky-blue;
    background-color: $color-deep-sky-blue;

    & h3 {
      color: $color-alice-blue;
    }

    & p {
      color: $color-alice-blue;
    }

    & svg {
      fill: $color-alice-blue;
    }
  }
}

.featuries__item--confed h3 {
  @include vp-1023 {
    margin-bottom: 8px;
  }

  @include vp-767 {
    margin-top: 13px;
    margin-bottom: 14px;
  }
}

.featuries__item--price h3 {
  @include vp-1023 {
    margin-top: 5px;
    margin-bottom: 8px;
  }

  @include vp-767 {
    margin-top: 13px;
    margin-bottom: 14px;
  }
}

