@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "roboto-condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed.woff2") format("woff2"), url("../fonts/roboto-condensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "roboto-condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "pt-mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Pt-mono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .main-header,
.wrapper .main-footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.button {
  display: inline-flex;
  padding: 10px 16px;
  font-family: "Roboto-condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 3px;
  transition: opacity 0.3s ease;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.button.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 40px;
  background-color: #00b2ff;
  color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal h2 {
  margin: 0;
  padding: 0;
  margin-bottom: 15px;
  font-family: "Roboto-condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 140%;
  font-weight: 400;
}
.modal p {
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
  line-height: 22px;
}
.modal__close-btn {
  position: absolute;
  top: 24px;
  right: 24px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__form .form__footer {
  flex-direction: column;
  align-items: center;
}

.modal__form--footer .form__button {
  width: 100%;
  margin-bottom: 20px;
}

.modal__form .modal__form--footer label {
  width: 100%;
}

.main-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .main-header {
    position: relative;
  }
}

.main-header__wrapper {
  display: grid;
  grid-template-columns: 28% auto 21%;
  column-gap: 4%;
  padding-top: 28px;
  padding-bottom: 30px;
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    grid-template-columns: 1fr 1fr;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    padding-top: 14px;
    padding-bottom: 20px;
  }
}

.main-header__contacts {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 1023px) {
  .main-header__contacts {
    margin-left: auto;
    align-items: flex-start;
    padding-top: 10px;
    padding-right: 12px;
  }
}
@media (max-width: 767px) {
  .main-header__contacts {
    min-width: auto;
    margin-left: auto;
    padding-top: 6px;
    padding-right: 2px;
  }
}

.main-header__contacts-link {
  display: flex;
  align-items: center;
  padding-left: 7px;
  line-height: 18px;
  color: #ffffff;
}
.main-header__contacts-link p {
  display: block;
}
@media (max-width: 1023px) {
  .main-header__contacts-link p {
    display: none;
  }
}
.main-header__contacts-link svg {
  fill: #00b2ff;
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .main-header__contacts-link {
    padding-left: 30px;
  }
}
@media (hover: hover) {
  .main-header__contacts-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-header__contacts-link.focus-visible:focus {
  opacity: 0.8;
}

.main-header__button {
  max-width: 160px;
  margin-left: auto;
  align-self: center;
  color: #f2f5f7;
  background-color: transparent;
  border: 1px solid #f2f5f7;
}
@media (max-width: 1023px) {
  .main-header__button {
    display: none;
  }
}

.logo__link {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 767px) {
  .logo__link svg {
    width: 35px;
    height: 35px;
  }
  .logo__link--footer svg {
    width: 55px;
    height: 55px;
  }
}

.logo__text {
  margin: 0;
  margin-left: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: #ffffff;
}
.logo__text--big {
  font-family: "Roboto-condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #f2f5f7;
}
.logo__text--small {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  opacity: 0.7;
}
@media (max-width: 767px) {
  .logo__text {
    display: none;
  }
  .logo__text--footer {
    display: flex;
  }
}

